.invoice-data-container h2
{
    color: white;
    text-align: center;
    padding-top: 15vh;
}
.invoice-data-container h2 a
{
    text-decoration: underline;
    color: rgb(165, 149, 149);
}
.invoice-data-container h2 a:hover
{
    color: white;

}

.container
{
    height: 100vh;
    background-color: rgba(0,0,0,0.5);
}
.table-container
{
    padding-top: 15vh;
    overflow: auto;
    width: 100%;
}
table
{
    /* border: 1px solid red; */
    width: 150%;
    overflow: auto;
}
th,
td
{
    color: white;
    height: 7vh;
    text-align: center;
    font-size: 20px;
}
.Actions
{
    /* width: 100px; */
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    gap: 10px;
}
.Actions button
{
    border: none;
    background-color: transparent;
}
.tt1 input
{
    color: white;
    text-align: center;
    font-size: 20px;
    height: 7vh;
}
.tt1 button
{
    text-align: center;
    font-size: 20px;
    height: 5vh;
    padding: 0 10px;
}