.about-container
{
    scrollbar-width: none;
    /* border:1px solid white; */
    padding-top: 25vh;
    overflow: auto;
    height: 75vh;
    background-color: rgba(0, 0, 0, 0.5);
}
.section-1-container
{
    color: white;
    margin: 0 4%;
    /* border:1px solid white; */
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
}
span
{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 5px;
}
span svg
{
    fill: white;
    /* color: white; */
    height: 80px;
    width: 150px;
}
h2
{
    font-size: 23px;
    text-align: center;
    margin-bottom: 10px;
}
p
{
    font-size: 17px;
    text-align:center;
}
.card
{
    padding: 10px 5px;
    width: 400px;
    height: 300px;
    /* border: 1px solid red; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.card:hover
{
    cursor: pointer;
    transform: translateY(-10px);
    box-shadow: rgba(248, 248, 248, 0.25) 0px 30px 60px -12px inset, rgba(255, 253, 253, 0.3) 0px 18px 36px -18px inset;
    /* box-shadow: rgba(242, 242, 248, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(252, 254, 255, 0.35) 0px -2px 6px 0px inset; */
    transition: 1s;
    background-color: rgba(0, 0, 0, 0.5);
}


/* section-2-container */
.section-2-container
{
    margin: 40px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
}
.container-1
{
    color: white;
    /* border: 1px solid red; */
    width: 600px;
    height: 490px;
}
.container-2
{
    color: white;
    /* border: 1px solid red; */
    width: 600px;
    height:490px;
}
.container-1 img
{
    width: 600px;
    height: 490px;
}
.container-2 h1
{
    text-align: left;
    font-size: 35px;
}
.container-2 p
{
    padding-top: 15px;
    text-align: left;
    font-size: 20px;
    line-height: 30px;
}
.container-2 .list
{
    display: grid;
    grid-template-columns: auto auto;
}
.container-2 .list h4
{
    cursor: pointer;
    padding-top: 15px;
    gap: 10px;
    font-size: 20px;
    line-height: 35px;
    display: flex;
    justify-content: left;
    align-items: center;
}
.container-2 .list h4:hover
{
    transform: translateX(3px);
}
.container-2 button
{
    cursor: pointer;
    margin-top: 12px;
    font-size: 20px;
    height: 9vh;
    width: 200px;
}
.container-2 button:hover
{
    transform: translateY(-2px);
    box-shadow: rgba(8, 8, 8, 0.25) 0px 30px 60px -12px inset, rgba(8, 8, 8, 0.3) 0px 18px 36px -18px inset; 
}