.invoice-display
{
    height: 100vh;
    width: 100%;
    overflow: auto;
    scrollbar-width: none;
    background-color: rgba(0,0,0,0.5);
}

/* form-1 */
.form-1
{
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin: 0 8%;
    padding: 6% 3% 0;
    /* border: 1px solid red; */
}
input
{
    border: 1px solid white;
    background-color: transparent;
}
.CD-header
{
    text-decoration: underline;
    margin-bottom: 3%;
    color: white;
    text-align: center;
}
.CD-header h2
{
    font-size: 30px;
}

.box
{
    /* border: 1px solid red; */
    display: flex;
    justify-content: left;
    align-items: center;
    gap: 10px;
    /* border: 1px solid red; */
    /* width: 100%; */
}
.box label
{
    color: white;
    padding: 5px 10px;
    font-size: 23px;
    /* border: 1px solid red; */
    width: 28vh;
    cursor: pointer;
}

.box input
{
    color: white;
    padding: 10px 10px;
    font-size: 23px;
    width: 80%;
    background-color: rgba(0,0,0,0.5);
}
.box1 input
{
    display: block;
    color: white;
    padding: 5px 10px;
    font-size: 23px;
    width: 80%;
}
.gender-input
{
    font-size: 23px;
    display: flex;
    justify-content: left;
    gap: 20px;
}
.gender-input span
{
    display: flex;
    gap: 5px;
}
.gender-input span input
{
    width: 5vh;
}
.gender-checkbox
{
    background-color: transparent;
    width: 81%;
    border: 1px solid white;
    display: flex;
    justify-content: left;
    align-items: center;
    gap: 10px;
    background-color: rgba(0,0,0,0.5);
    /* border: 1px solid red; */
}
.gender-checkbox input
{
    outline: none;
    /* border-radius: 50%; */
    width: 50px;
}
.gender-checkbox span
{

    font-size: 23px;
    display: flex;
}
/* .Course-box select
{
    outline: none;
    font-size: 20px;
    border: none;
    padding: 8px 10px;
    border: 1px solid red;
    width: 82%;
} */
.batch-box option
{
    color: black;
}
.batch-box select
{
    color: white;
    background-color: rgba(0,0,0,0.5);
    outline: none;
    font-size: 20px;
    border: none;
    padding: 8px 10px;
    border: 1px solid white;
    width: 82%;
}
.fees-box input
{
    color: white;
}
.btn
{
    margin: 4vh 0 8vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.btn button
{
    border: none;
    border-radius: 25px;
    font-size: 25px;
    height: 50px;
    width: 150px;
}
/* .fees-box label */
.fees-box input
{
    color: white;
    width: 80%;
}
.input-error {
    border: 2px solid red; /* Change to your desired error color */
}