@import url('https://fonts.googleapis.com/css2?family=Inder&family=Margarine&family=Playwrite+DE+Grund:wght@100..400&family=Satisfy&display=swap');
body
{

  font-family: "Inder", serif;
  font-style: normal;
  background: url(https://www.wallpapertip.com/wmimgs/66-666819_website-design-background-creative-background-image-for-website.jpg);
  background-size:cover;
  background-repeat: no-repeat;
  /* position: fixed; */
  background-color: grey;
  /* width: 100%; */
}
.navbar-container
{
  position: fixed;
  width: 100%;
}
.side-navbar
{
  /* width: 100%; */
  padding: 0 2%;
  /* border: 1px solid red; */
  display: flex;
  background-color: rgba(26, 25, 25, 0.5);
  justify-content: space-between;
  align-items: center;
  height: 10vh;
  color: white;
}
.navbar-page-name-box
{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
}
h2
{
  font-size: 25px;
}

.navbar-page-name-box a
{
  border: 1px solid transparent;
  padding: 3px 5px;
  border-radius: 10px;
  font-size: 23px;
  text-decoration: none;
  color: white;
}
.navbar-page-name-box a:hover
{
  text-decoration: underline;
  transform: translateY(-3px);
  transition: 1s;
}
.navbar-page-name-box a.active
{
  text-decoration: underline;
  transform: translateY(-3px);
  transition: 1s;
}
