*
{
    padding: 0;
    margin: 0;
}
.main-container
{
    /* padding-top: 5vh; */
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    height: 100vh;
    width: 100%;
}
.content-container
{
    width: 50%;
    /* border: 1px solid red; */
    color: white;
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items: left;
    gap: 20px;
    padding-left: 10%;
}
.content-container h1
{
    font-size: 60px;
}
.content-container p
{
    text-align: justify;
    font-size: 20px;
}
.content-btn
{
    display: flex;
    gap: 15px;
}
.content-container button
{
    cursor: pointer;
    border: none;
    font-size: 22px;
    height: 60px;
    width: 150px;
}
.content-container button:hover
{
    box-shadow: rgba(8, 8, 8, 0.25) 0px 30px 60px -12px inset, rgba(8, 8, 8, 0.3) 0px 18px 36px -18px inset;
    transform: translateY(-2px);
}

.content-container-2
{
    display: flex;
    justify-content: center;
    align-items: center;
    /* border: 1px solid red; */
    width: 50%;
}
.content-container-2 img
{
    margin-top: -50px;
    /* border: 1px solid red; */
}
