.main-container
{
    background-color: rgba(0, 0, 0, 0.5);
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.card-1
{
    width: 710px;
}
.card-2,
.card-3
{
    width: 350px;
}
.img-box-1,
.img-box-2,
.img-box-3,
.img-box-4
{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
span
{
    padding: 10px 0;
    color: white;
    font-size: 25px;
}
.card-4 
{
    /* margin-top: -6px; */
    /* border: 1px solid red; */
    width: 500px;
    height: 80vh;
    /* padding-bottom: 4px; */

}
.content-box img:hover
{
    cursor: pointer;
    transform: scale(1.01);
}

.card-4 img
{
    /* border: 1px solid red; */
    height: 90%;
}
.content-box
{
    margin-top: 9vh;
    /* border: 1px solid red; */
    display: flex;
    gap: 10px;
}
.mid-img
{
    /* border: 1px solid red; */
    display: flex;
    gap: 10px;
}
.content-box a
{
    text-decoration: none;
}